/* typography vars */
@import url("https://fonts.googleapis.com/css?family=Nunito+Sans:300,400,600,700|Open+Sans:300,400,600,700&display=swap");
/* Basic config */
:root {
  --orange-brand: #FF9F43;
  --yellow-brand: #FFBF2E;
  --cyan-brand: #01A3A4;
  --blue-brand: #00316E;
  --background-color: #FAFAFA;
  --text-color: #707070;
  --heading-color: #202020;
  --text-line-height: 1.6;
  --font-primary: Open Sans, sans-serif;
  --font-display: Nunito Sans, sans-serif;
  --font-size--small: 1.4rem;
  --font-size--medium: 1.7rem;
  --font-size--large: 2rem;
  --font-size--heading: 5vmin;
  --shadow--dark: 0 2rem 6rem rgba(#202020, .4);
  --shadow--light: 0 2rem 6rem rgb(#fff, .3);
  --font-weight--light: 300;
  --font-weight--noramal: 400;
  --font-weight--semi-bold: 600;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-size: var(--font-size--small);
  line-height: var(--text-line-height);
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: var(--font-primary);
  font-weight: 300;
}

.site-container {
  display: grid;
  grid-template-rows: 6rem 80vh min-content repeat(2, min-content) 30vw repeat(7, min-content);
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  background: linear-gradient(90deg, #fff 20px, transparent 1%) center, linear-gradient(#fff 20px, transparent 1%) center, #c7c4c4;
  background-size: 22px 22px;
}

@media screen and (min-width: 75em) {
  .site-container {
    display: grid;
    grid-template-rows: 6rem 80vh 15vh repeat(2, min-content) 30vw repeat(7, min-content);
    grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  }
}

a {
  color: #000;
  text-decoration: none;
  word-wrap: break-word;
}

a:hover, a:focus, a:active, a:visited, a:link {
  text-decoration: none;
  outline: 0;
}

hr {
  margin-top: 3rem;
  margin-bottom: 3.5rem;
  border-width: 0;
  border-top: 1px solid #ccc;
}

p {
  margin: 1.2rem 0;
}

ul {
  padding: 0;
  list-style: none;
}

ol {
  padding-left: 0;
  margin-top: 0;
}

li {
  margin-bottom: 1rem;
}

img,
video {
  display: block;
  max-width: 100%;
}

figure {
  margin: 0 auto;
  padding-bottom: 2.4rem;
}

figure img {
  display: block;
  margin: 0 auto;
}

caption,
figcaption {
  font-size: 1rem;
  font-style: italic;
  text-align: center;
}

table {
  border-collapse: collapse;
  border-color: gray;
  border-spacing: 2px;
  max-width: 100%;
  width: 100%;
}

table thead th {
  vertical-align: bottom;
  border-bottom: 1px solid #ccc;
}

table th,
table td {
  padding: 0.6rem;
  vertical-align: top;
  border-top: 1px solid #ccc;
}

table th,
table tfoot td {
  font-weight: 700;
  text-align: left;
}

table td {
  border-bottom: 1px solid #ccc;
  padding: .6rem;
  text-align: left;
  vertical-align: top;
}

textarea,
select {
  height: 38px;
  padding: 6px 10px;
  background-color: #fff;
  border: 1px solid #e2e2e2;
  box-shadow: none;
}

textarea:focus,
select:focus {
  border: 1px solid #9534f0;
  outline: 0;
}

input[type="email"], input[type="number"], input[type="search"], input[type="text"], input[type="tel"], input[type="url"], input[type="password"] {
  appearance: none;
  background-color: #fff;
  border: 1px solid #e2e2e2;
  box-shadow: none;
  height: 38px;
  padding: 6px 10px;
  -moz-appearance: none;
  -webkit-appearance: none;
}

input[type="email"]:focus, input[type="number"]:focus, input[type="search"]:focus, input[type="text"]:focus, input[type="tel"]:focus, input[type="url"]:focus, input[type="password"]:focus {
  outline: 0;
  border: 1px solid #604ec957;
}

textarea {
  appearance: none;
  min-height: 65px;
  padding-bottom: 6px;
  padding-top: 6px;
  -moz-appearance: none;
  -webkit-appearance: none;
}

label,
legend {
  display: block;
  font-weight: 600;
  margin-bottom: .5rem;
}

fieldset {
  padding: 0;
  border-width: 0;
}

input[type="checkbox"], input[type="radio"] {
  display: inline;
}

label > .label-body {
  display: inline-block;
  font-weight: normal;
  margin-left: .5rem;
}

pre {
  overflow: auto;
}

code {
  background: gainsboro;
  border-radius: 50%;
  border: 1px solid #ccc;
  font-size: 90%;
  margin: 0 .2rem;
  padding: .2rem .5rem;
  white-space: nowrap;
}

pre > code {
  display: block;
  padding: 1rem 1.5rem;
  white-space: pre;
}

.mb-20 {
  margin-bottom: 2rem;
}

.container {
  width: 1160px;
  margin: 0 auto;
  padding: 0 2rem;
  max-width: 100%;
}

/* typography */
.heading-1, .heading-2, .heading-3, .heading-4 {
  font-family: var(--font-display);
  font-size: var(--font-size--heading);
  color: var(--heading-color);
  font-weight: 400;
}

.heading-2 {
  --font-size--heading: 3rem;
}

.heading-2--light {
  font-weight: 600;
  line-height: 1;
  width: 60%;
  margin-bottom: 2rem;
  --heading-color: #FAFAFA;
}

.heading-2--dark {
  text-align: left;
  font-weight: 600;
  line-height: 1;
  width: 60%;
  margin-bottom: 2rem;
  --heading-color: #FFBF2E;
}

.heading-3--light {
  --font-size--heading: 1.6rem;
  --heading-color: #FFBF2E;
}

.heading-3--large {
  text-align: center;
  --font-size--heading: 2rem;
}

.heading-4 {
  font-variant: small-caps;
  --font-size--heading: 1.6rem;
}

.heading-4--light {
  letter-spacing: 1px;
  --heading-color: #707070;
}

.heading-4--white {
  --heading-color: #FAFAFA;
  --font-size--heading: 1.4rem;
}

.heading-4--dark {
  --heading-color: #171717;
}

.page-section {
  margin: 15rem 0;
}

.btn {
  background-color: transparent;
  color: #fff;
  border: none;
  border-radius: 0;
  font-family: var(--font-display);
  text-transform: uppercase;
  padding: 1.8rem 3rem;
  font-weight: 600;
  cursor: pointer;
  transition: all .2s ease-in-out;
  font-size: 1.2rem;
}

.btn--orange {
  background-color: var(--orange-brand);
}

.btn--orange:hover {
  background-color: #ffac5d;
}

.btn--inverse {
  border: 2px solid var(--orange-brand);
  padding: 1.5rem 4rem;
}

.btn--inverse:hover {
  background-color: #ffac5d;
}

.header {
  background-color: #0065b7;
  grid-column: full-start / full-end;
  position: sticky;
  z-index: 1020;
  top: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
}

.logo {
  grid-column: col-start 1 / col-end 1;
  z-index: 990;
  width: 10rem;
  height: 10rem;
}

.menu {
  grid-column: col-start 5 / col-end 8;
}

.menu__items {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 6rem;
}

.menu__item {
  display: inline-block;
  margin-bottom: 0px;
  text-transform: uppercase;
}

.menu__item:not(:last-child) {
  margin-right: 1.2rem;
}

.menu__item > * {
  font-size: 1.2rem;
  padding: 2.25rem 1rem;
}

.menu__link {
  color: #fff;
  font-weight: 300;
}

.menu__link:hover, .menu__link:focus {
  background-color: #FF9F43;
  color: #FAFAFA;
}

.large-hero {
  grid-column: full-start / full-end;
  background: url("/assets/images/hero-3.jpg") no-repeat bottom 90% center/cover;
  background-attachment: fixed;
  color: white;
  display: grid;
  grid-template-columns: repeat(8, [col-start] minmax(min-content, 14rem) [col-end]);
  justify-content: center;
  align-items: center;
}

.large-hero__content {
  grid-column: 1 / -1;
  padding: 0 2rem;
}

@media screen and (min-width: 37.5em) {
  .large-hero__content {
    grid-column: col-start 1 / col-end 5;
  }
}

.large-hero__title {
  color: #ffac5d;
  line-height: 1;
  margin: .5rem 0 4rem;
  font-size: 3rem;
}

@media screen and (min-width: 37.5em) {
  .large-hero__title {
    font-size: 5vmin;
  }
}

.large-hero__subtitle {
  font-family: "Nunito Sans", sans-serif;
  color: #FAFAFA;
  font-style: italic;
}

.large-hero__description {
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.info {
  grid-column: full-start / full-end;
  background-color: #00244e;
  padding-bottom: 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  align-content: center;
}

@media (min-width: 37.5em) and (max-width: 65em) {
  .info {
    display: grid;
    grid-template-columns: repeat(2, minmax(30rem, 1fr));
    align-content: center;
    justify-items: center;
  }
}

@media screen and (min-width: 75em) {
  .info {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
  }
}

.info__item {
  color: #FAFAFA;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.info__icon {
  fill: #bbbbbb;
  width: 6rem;
  height: 6rem;
}

.info__text {
  margin-top: .5rem;
  margin-bottom: 0;
  color: #FFBF2E;
}

.features {
  background: linear-gradient(90deg, #fff 20px, transparent 1%) center, linear-gradient(#fff 20px, transparent 1%) center, #c7c4c4;
  background-size: 22px 22px;
  background-color: #fff;
  grid-column: center-start / center-end;
  align-items: start;
  display: grid;
  grid-template-columns: repeat(1, minamax(min-content, 1fr));
  grid-gap: 3rem;
}

@media screen and (min-width: 37.5em) {
  .features {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 5rem;
  }
}

@media screen and (min-width: 75em) {
  .features {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 6rem;
  }
}

.feature {
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-column-gap: 1rem;
  grid-row-gap: .3rem;
  padding: 2rem;
}

.feature__icon {
  fill: var(--orange-brand);
  width: 8rem;
  height: 8rem;
  grid-row: 1 / span 2;
  align-self: start;
  transform: translateY(-2.7rem);
  transition: .3s all ease-out;
}

.feature__icon:hover {
  fill: var(--yellow-brand);
  transform: translateY(-3.3rem) scale(1.05);
}

.feature__text {
  font-size: var(--font-size-small);
}

.cta-equipment {
  grid-column: full-start / full-end;
  height: 45vh;
  background: linear-gradient(to right, rgba(93, 117, 163, 0.7), rgba(2, 2, 2, 0.7), rgba(0, 0, 0, 0.5)), url("/assets/images/cta-5.jpg") left 30% bottom 60%/cover no-repeat, #020202;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.cta-equipment p {
  width: 60rem;
  color: #FAFAFA;
  font-size: var(--font-size-small);
  text-align: center;
  margin: 1rem 0 4rem 0;
}

.team__pictures {
  grid-column: full-start / col-end 2;
  grid-row: 6 / 8;
  background: url(/assets/images/team-5.jpg) no-repeat center center/cover;
  background-color: #171717;
}

.team__content {
  grid-column: col-start 3 / full-end;
  background-color: #004962;
  color: #fff;
  padding: 6rem 8vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.team__content p {
  margin-bottom: 3rem;
}

.team__instructors {
  grid-column: col-start 3 / full-end;
  background-color: #202020;
  padding: 3rem 0;
}

.instructor__list {
  display: flex;
  justify-content: space-evenly;
  padding: 5rem 5vw;
}

.instructor__img {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  margin-right: 1rem;
}

.instructor__details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.instructor__details:not(:last-child) {
  margin-right: 2rem;
}

.instructor__hours {
  margin: 0;
}

.tours__activites {
  background-color: #001C57;
  grid-column: center-start / center-end;
}

.tours__cta {
  grid-column: full-start / full-end;
  height: 50vh;
  background: linear-gradient(to right, rgba(39, 53, 8, 0.7), rgba(2, 2, 2, 0.7), rgba(0, 0, 0, 0.5)), url("/assets/images/cta-7.jpg") left 30% bottom 30%/cover no-repeat, #020202;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tours__cta p {
  width: 60%;
  color: #FAFAFA;
  font-size: var(--font-size-medium);
  text-align: left;
  margin: 1rem 0 4rem 0;
}

.show {
  grid-column: full-start / full-end;
  background: linear-gradient(to right, rgba(28, 52, 80, 0.7), rgba(2, 2, 2, 0.7), rgba(0, 0, 0, 0.5)), url(/assets/images/hero-2.jpg) no-repeat center center/cover;
  display: grid;
  grid-template-columns: [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end]) [center-end] minmax(6rem, 1fr) [full-end];
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 6rem;
  padding-top: 12rem;
}

.show__video {
  grid-column: center-start / col-end 4;
  background: url(/assets/images/cta-1.jpg) no-repeat center center/cover;
  height: 40rem;
  padding: 6rem;
  text-align: center;
}

.show__content {
  grid-column: col-start 5 / center-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.show__counter {
  grid-column: center-start / center-end;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}

.counter__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.counter__number {
  font-size: 6rem;
  color: #FF9F43;
  font-weight: 600;
}

.counter__icon {
  fill: #FFBF2E;
  width: 8rem;
  height: 8rem;
}

.testimonals {
  grid-column: full-start / full-end;
  padding: 10rem 2vw;
}

.testimonial {
  margin: 0 auto;
  max-width: 50rem;
}

.testimonial__user {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.testimonial__img {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  border: 5px solid #FAFAFA;
  margin-bottom: 3rem;
  box-shadow: 0 0 2rem rgba(32, 32, 32, 0.5);
}

.testimonial__name {
  font-size: 2rem;
  color: #FF9F43;
  font-weight: 600;
}

.testimonial__text {
  text-align: center;
}

.gallery {
  background-color: #171717;
  grid-column: full-start / full-end;
}

.footer {
  background-color: #191D32;
  grid-column: full-start / full-end;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, min-content));
  justify-content: center;
  grid-gap: 3rem;
  padding: 6rem 0;
}

.footer__list {
  text-transform: uppercase;
  font-size: 1.1rem;
}

.footer__link {
  color: #a8a8a8;
}

.footer__link:hover {
  color: #87cefa;
}

.bg--light {
  background-color: #fff;
}

.bg--dark {
  background-color: #000;
}
